























import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import intervalList from '@/constants/intervalList';
import recordTypeList from '@/constants/recordTypeList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
@Component({
  components:{Tabs}
})
export default class Statistics extends Vue{
  time(string:string){
    const day =dayjs(string)
    const now =dayjs()
    if(day.isSame(now,'day')){
      return '今天'
    }else if(day.isSame(now.subtract(1,'day'),'day')){
      return '昨天'
    }else if(day.isSame(now.subtract(2,'day'),'day')){
      return '前天'
    }else if(day.isSame(now,'year')){
      now.format('M月D日')
    }else{
      now.format('YYYY年M月D日')
    }
  }
  tagString(tags:Tag[]){
    console.log(tags)
   const aaa= tags.join(',')
    console.log(aaa);
    return tags.length===0?'无':tags.map(t=>t.name).join('，')
  }
  get recordList(){
    return (this.$store.state as RootState).recordList
  }
  get groupedList() {
    const {recordList} = this;

    const newList = clone(recordList)
        .filter(r => r.type === this.type)
        .sort((a, b) => dayjs(b.creatAt).valueOf() - dayjs(a.creatAt).valueOf());
    if (newList.length === 0) {return [];}
    type Result = { title: string, total?: number, items: RecordItem[] }[]
    const result: Result = [{title: dayjs(newList[0].creatAt).format('YYYY-MM-DD'), items: [newList[0]]}];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.creatAt), 'day')) {
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.creatAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => {
        console.log(sum);
        console.log(item);
        return sum + item.amount;
      }, 0);
    });
    return result;
  }
  type='-';
  interval='day'
  intervalList=intervalList
  recordTypeList=recordTypeList

  created(){
    this.$store.commit('fetchRecord')
  }
}
